import React, { useState } from 'react'
import { useDispatch } from 'react-redux';
import "./AddArticle.css"

export default function AddArticle() {

    const [article, setArticle] = useState({
        title: "",
        body: "",
        link: ""
    })

    const dispatch = useDispatch();

    const handleForm = (e) => {
        e.preventDefault()
        dispatch({
            type: 'ADD_ARTICLE',
            payload: article
        })

        setArticle({
            title: "",
            body: "",
            link: ""
        })
    }

    const handleInputs = (e) => {
        let newValues
        if (e.target.classList.contains('article-title')) {
            newValues = {...article, title: e.target.value}
        } else if (e.target.classList.contains('article-body'))
        {
            newValues = {...article, body: e.target.value}
        }
        setArticle(newValues)
    }
    
    return (
        <form onSubmit={handleForm} className="add-article-form">
            <label> Title </label>
            <input type="text"
                className="article-title"
                placeholder='Enter article title'
                onChange={handleInputs}
                value={article.title}/>

            <label> Body </label>
            <textarea
                className="article-body"
                placeholder='Enter article body'
                onChange={handleInputs}
                value={article.body}/>
            <input type="submit" value="Submit"/>
        </form>
    )
}
