import React from 'react'
import { Link } from 'react-router-dom'
import './Article.css'
import { useLocation } from 'react-router'

export default function Article(props) {
    let showBody = true
    if (props.showBody === undefined) {
        showBody = true
    } else {
        showBody = props.showBody
    }

    const location = useLocation()

    return (
        <div className={ showBody ? 'article' : 'card'}>
            {showBody && <h2> Your Article: </h2>}

            <h2>{(props.title !== undefined) ? props.title : location.state.title}</h2>

            <p>{showBody && ((props.body !== undefined) ? props.body : location.state.body)}</p>

            {!showBody &&
                <Link to={{
                pathname: `Articles/${props.title}`,
                state: {
                    title: props.title,
                    body: props.body
                }
                }}>
                read
                </Link>
            }
        </div>
    )
}
