import React, { useState, useEffect } from 'react'
import './Menu.css'
import { NavLink } from 'react-router-dom'

export default function Menu() {
    const [toggleMenu, setToggleMenu] = useState(false)
    const [largeur, setLargeur] = useState(window.innerWidth)

    const toggleAddnav = () => {
        setToggleMenu(!toggleMenu)
    }
    
    useEffect(() => {

        const changeWidth = () => {
            setLargeur(window.innerWidth)
        }

        window.addEventListener('resize', changeWidth)

        return () => {
            window.removeEventListener('resize', changeWidth)
        }    
    }, [])

    return (
        <nav>
            { (toggleMenu || largeur > 500) && (
                <ul className='list'>
                    <NavLink className="item" exact activeClassName="current" to="/">
                    <li>Home</li>
                    </NavLink>

                    <NavLink className="item" exact activeClassName="current" to='/Write'>
                    <li>Write</li>
                    </NavLink>

                    <NavLink className="item" exact activeClassName="current" to='/Contact'>
                    <li>Contact</li>
                    </NavLink>
                </ul>
            )}
            <button
                className="btn"
                onClick={toggleAddnav}>
                    Menu
            </button>
        </nav>
    )
}
